#contactFont {
  font-family: 'Roboto', sans-serif;
  text-align: center;
  vertical-align: middle;
}

#contactTitle {
  font-size: 40px;
  margin-bottom: 40px;
  margin-top: 40px;
  letter-spacing: 1px;
}

@media screen and (max-width: 425px) {
  #contactTitle {
    font-size: 25px;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: 10px;
    margin-right: 10px;
  }
}
